/**
 * pioneerÂè™Âú®ÂàùÊúüÔºàÊ≤°ÊúâcontainerÂíåË∂≥Â§üÁöÑextensionÊó∂Ôºâ‰ΩøÁî®ÔºåË¥üË¥£‰ª•‰∏ãÂ∑•‰ΩúÔºö
 * 1. ÈááÈõÜËÉΩÈáè
 * 2. Êê¨ËøêËÉΩÈáèÂà∞spawnÊàñextension
 * 3. ‰øÆÂª∫construction site
 * 4. ÂçáÁ∫ßÊéßÂà∂Âô®
 * @param {*} creep 
 */
export const doPioneer = function (creep) {

    // ÂàÜÈÖçsourceÔºöÁî®Ê≠£Á°ÆÁöÑËø≠‰ª£ÊñπÂºèÂπ∂Â¢ûÂä†Á©∫Ê£ÄÊü•ÔºåÈÅøÂÖçËÆøÈóÆ undefined
    if (!creep.memory.targetSourceId) {
        const sourceCountById = {};
        const creepsInMyRoom = creep.room.find(FIND_MY_CREEPS);
        for (const other of creepsInMyRoom) {
            if (!other || !other.memory) continue;
            const otherSourceId = other.memory.targetSourceId;
            if (otherSourceId) {
                sourceCountById[otherSourceId] = (sourceCountById[otherSourceId] || 0) + 1;
            }
        }

        // Ê†πÊçÆroom.memory.source.idÈÄâÊã©source
        const source = creep.room.memory.source;
        for (const s of source) {
            const count = sourceCountById[s.id] || 0;
            if (count < s.count) {
                creep.memory.targetSourceId = s.id;
                break;
            }
        }
    }

    // Áä∂ÊÄÅÊú∫Ôºö
    // 1. Â¶ÇÊûúÊ≤°ÊúâÊê∫Â∏¶ËÉΩÈáèÔºåÂéªÈááÈõÜ
    // 2. Â¶ÇÊûúËÉΩÈáèÈááÊª°ÔºåÊåâÁÖß‰ºòÂÖàÁ∫ßÊâßË°åÔºöÊê¨Ëøê-‰øÆÂª∫-ÂçáÁ∫ß
    if (creep.store[RESOURCE_ENERGY] === 0) {
        creep.memory.workingState = 'harvesting';
    } else if (creep.store.getFreeCapacity() === 0) {
        creep.memory.workingState = 'working';
    }

    // ÊâßË°åÈÄªËæë
    if (creep.memory.workingState === 'harvesting') {
        const source = Game.getObjectById(creep.memory.targetSourceId);
        if (creep.harvest(source) === ERR_NOT_IN_RANGE) {
            creep.moveTo(source, {visualizePathStyle: {stroke: '#ffaa00'}});
            creep.say('‚õèÔ∏è');
        }
    } else if (creep.memory.workingState === 'working') {
        // ‰ºòÂÖàÊê¨ËøêËÉΩÈáèÂà∞spawnÊàñextension
        const target = creep.pos.findClosestByPath(FIND_STRUCTURES, {
            filter: (structure) => {
                return (structure.structureType === STRUCTURE_SPAWN 
                        || structure.structureType === STRUCTURE_EXTENSION) 
                    && structure.store.getFreeCapacity(RESOURCE_ENERGY) > 0;
            }
        });
        if (target) {
            if (creep.transfer(target, RESOURCE_ENERGY) === ERR_NOT_IN_RANGE) {
                creep.moveTo(target, {visualizePathStyle: {stroke: '#ffffff'}});
                creep.say('üöö');
            }
            return;
        }
        // ÂÖ∂Ê¨°‰øÆÂª∫
        const constructionSite = creep.pos.findClosestByPath(FIND_CONSTRUCTION_SITES);
        if (constructionSite) {
            if (creep.build(constructionSite) === ERR_NOT_IN_RANGE) {
                creep.moveTo(constructionSite, {visualizePathStyle: {stroke: '#ffffff'}});
                creep.say('üöß');
            }
            return;
        }
        // ÊúÄÂêéÂçáÁ∫ßÊéßÂà∂Âô®
        if (creep.upgradeController(creep.room.controller) === ERR_NOT_IN_RANGE) {
            creep.moveTo(creep.room.controller, {visualizePathStyle: {stroke: '#ffffff'}});
            creep.say('‚ö°');
        }
    }
}